import React from 'react';
import PropTypes from 'prop-types';

// eslint-disable-next-line react/prefer-stateless-function
export class EnhancedTransitionComponentBase extends React.Component {}

function transitionEnhancer(TransitionComponent) {
  class EnhancedTransitionComponent extends EnhancedTransitionComponentBase {
    constructor(props) {
      super(props);
      this.state = {
        show: false,
      };
      const { timeout = null } = this.props;
      if (timeout == null) throw new Error('timeoutenhance');
      this.timeout = timeout;
    }

    componentDidMount() {
      const { show: willShow } = this.props;
      // console.log('', this.state, 'willShow', willShow);
      this.setState({
        show: willShow,
      });
    }

    componentWillReceiveProps(nextProps) {
      const { show: willShow } = nextProps;
      // console.log('update', this.state, 'willShow', willShow);
      this.setState({
        show: willShow,
      });
    }

    render() {
      // showTransitionComponent
      const {
        show: willShow, children, timeout, ...rest
      } = this.props;

      const { show } = this.state;

      // console.log('render', this.state);
      // console.warn('Children style');

      return (
        <TransitionComponent
          {...rest}
          timeout={timeout}
          in={show}
        >
          {React.Children.only(children)}
        </TransitionComponent>
      );
    }
  }

  EnhancedTransitionComponent.propTypes = {
    children: PropTypes.node.isRequired,
    show: PropTypes.bool,
    timeout: PropTypes.number,
  };

  EnhancedTransitionComponent.defaultProps = {
    timeout: 300,
    show: false,
  };

  return EnhancedTransitionComponent;
}

export default transitionEnhancer;
